import { testmode } from './Setting';
const GA_MEASUREMENT_ID = 'G-57D75MF8YC'
window['ga-disable-' + GA_MEASUREMENT_ID] = (testmode)?true:false;

function SendToGoogleAnalytics({name, delta, id}) {
  // Assumes the global `gtag()` function exists, see:
  // https://developers.google.com/analytics/devguides/collection/gtagjs
  window.gtagPageview({
    name:name,
    event_category: 'Web Vitals',
    // The `id` value will be unique to the current page load. When sending
    // multiple values from the same page (e.g. for CLS), Google Analytics can
    // compute a total by grouping on this ID (note: requires `eventLabel` to
    // be a dimension in your report).
    event_label: id,
    // Google Analytics metrics must be integers, so the value is rounded.
    // For CLS the value is first multiplied by 1000 for greater precision
    // (note: increase the multiplier for greater precision if needed).
    value: Math.round(name === 'CLS' ? delta * 1000 : delta),
    // Use a non-interaction event to avoid affecting bounce rate.
    non_interaction: true,
  });
}
export function SelectContenttoGoogleAnalytics(_content_type, _item_id){
  window.gtag('event', 'select_content', {
    content_type: _content_type,
    item_id: _item_id
  });
  console.log({
    content_type: _content_type,
    item_id: _item_id
  });
}

export function SendEventToGoogleAnalytics(action, category, label, value) {
  window.gtag('event', action, 
  {
    event_category: category,
    event_label: label,
  }
  );
  console.log({action, category, label, value});
}

export default SendToGoogleAnalytics;